.devices {

  &__content {
    display: flex;
    justify-content: center;
    padding-top: 80px;
  }

  &__photo {

    img {
      max-width: 100%;
    }
  }

  &__title {
    line-height: 2;
    padding-top: 80px;

    h2 {
      color: #555555;
      font-size: 30px;

      @media screen and (max-width: 1128px) {
        font-size: 26px;
      }

      @media screen and (max-width: 692px) {
        font-size: 20px;
      }
    }

    p {
      font-size: 16px;
      color: #8c8c8c;

      @media screen and (max-width: 1128px) {
        font-size: 12px;
      }

      @media screen and (max-width: 1128px) {
        font-size: 10px;
      }
    }
  }

  &__menu {
    list-style: none;
    margin-left: 10px;
    color: #8c8c8c;

    @media screen and (max-width: 692px) {
     margin-left: 0;
    }
  }

  &__list {
    position: relative;
    padding-left: 50px;

    &:hover {
      cursor: pointer;
      opacity: 0.5;
    }

    &:before {
      content: '';
      background: url("../img/png/arrow-devices.png") 0 0 no-repeat;
      width: 21px;
      height: 21px;
      position: absolute;
      top: 5px;
      left: 0;
    }
  }
}